@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #f9fbfd;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

html {
  scroll-behavior: smooth;
}

.wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 103px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  z-index: 1;
}
@media (max-width: 767px) {
  .header {
    min-height: 132px;
  }
}

.header__container {
  display: flex;
  padding: 35px 103px;
  justify-content: space-between;
}
@media (max-width: 1439px) {
  .header__container {
    padding: 35px 103px;
  }
}
@media (max-width: 1023px) {
  .header__container {
    padding: 30px 45px 51px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding: 20px 15px 46px;
  }
}

.header__button {
  display: none;
  transition: all 0.3s ease;
}
@media (max-width: 767px) {
  .header__button {
    display: block;
    padding: 0;
    width: 24px;
    height: 14px;
  }
}

.logo__icon {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .logo__icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__icon {
    width: 42px;
    height: 22px;
  }
}

.navigation__list {
  display: flex;
  max-width: 950px;
  margin: 0;
  margin-top: -3px;
  padding: 0;
  list-style-type: none;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .navigation__list {
    margin-top: -13px;
    flex-direction: row;
  }
}
@media (max-width: 767px) {
  .navigation__list {
    flex-direction: column;
    align-items: center;
    gap: 8px;
    overflow: scroll;
    flex-wrap: nowrap;
  }
}

.navigation__item {
  padding: 10px 0;
}
@media (max-width: 767px) {
  .navigation__item {
    padding: 10px 0 8px;
  }
}

.navigation__item:not(:last-of-type) {
  margin-right: 33px;
}
@media (max-width: 1023px) {
  .navigation__item:not(:last-of-type) {
    margin-right: 24px;
  }
}

.navigation__link {
  position: relative;
  padding: 10px 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  color: #f9fbfd;
}
.navigation__link::before {
  position: absolute;
  content: "";
  width: 0;
  height: 1px;
  bottom: 0;
  left: 50%;
  background-color: #f9fbfd;
  transition: width, left, 0.3s ease;
}
@media (hover: hover) {
  .navigation__link:hover:not(.focus-visible)::before {
    width: 100%;
    bottom: 0;
    left: 0;
  }
}
@media (max-width: 1023px) {
  .navigation__link {
    font-size: 14px;
    line-height: 17px;
  }
  .navigation__link::before {
    bottom: 20px;
  }
}
@media (max-width: 767px) {
  .navigation__link {
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 18px;
    line-height: 22px;
    font-weight: 500;
    color: #011c40;
    padding: 10px 0 8px;
  }
  .navigation__link::before {
    bottom: 0;
    background-color: #011c40;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .navigation__link:hover:not(.focus-visible)::before {
    width: 100%;
    left: 0;
  }
}

@media (max-width: 1023px) {
  .navigation--footer .navigation__link {
    font-size: 12px;
    line-height: 15px;
  }
}
@media (max-width: 767px) {
  .navigation--footer {
    margin-bottom: 22px;
  }
  .navigation--footer .navigation__list {
    align-items: flex-start;
  }
  .navigation--footer .navigation__link {
    padding: 0;
    font-size: 12px;
    line-height: 15px;
    color: #f9fbfd;
  }
  .navigation--footer .navigation__link::before {
    background-color: #f9fbfd;
  }
  .navigation--footer .navigation__item {
    padding: 0;
  }
}

.burger {
  display: flex;
}
.burger .burger__button {
  position: relative;
  display: none;
  margin-top: 8px;
  border: none;
  background-color: transparent;
}
.burger .burger__button::after, .burger .burger__button::before {
  background-color: #f9fbfd;
  position: absolute;
  content: "";
  width: 24px;
  height: 2px;
}
.burger .burger__button::before {
  top: -4px;
  left: 0;
  box-shadow: 0 6px 0 0 #f9fbfd;
}
.burger .burger__button::after {
  left: 0;
  bottom: 4px;
}
@media (max-width: 767px) {
  .burger {
    flex-direction: column;
  }
  .burger .burger__container {
    flex-direction: column;
    align-items: center;
    padding: 0;
    background-color: #011c40;
  }
  .burger .burger__wrapper {
    width: 100%;
    background-color: #f9fbfd;
  }
  .burger .burger__list {
    flex-direction: column;
    align-items: center;
  }
}

.burger__wrapper {
  top: 0;
  left: 0;
  transition: 0.3s ease;
}

@media (max-width: 767px) {
  .burger--close {
    flex-direction: row;
  }
  .burger--close .burger__container {
    flex-direction: row;
    padding: 20px 15px 84px;
    align-items: flex-start;
    background-color: transparent;
  }
  .burger--close .burger__wrapper {
    position: absolute;
    width: 100vw;
    height: 100vh;
    transform: translateX(200%);
    background-color: rgba(1, 24, 64, 0.7);
  }
  .burger--close .burger__button {
    display: block;
  }
}

@media (max-width: 767px) {
  .burger--open {
    flex-direction: row;
    min-height: 68px;
  }
  .burger--open .burger__container {
    flex-direction: row;
    padding: 0 15px;
    align-items: flex-start;
    background-color: transparent;
  }
  .burger--open .burger__logo {
    position: absolute;
    left: 50px;
    top: 20px;
    z-index: 2;
  }
  .burger--open .burger__icon {
    fill: #011c40;
  }
  .burger--open .burger__button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 7px;
    top: 24px;
    width: 24px;
    height: 14px;
    margin-top: 0;
    z-index: 2;
  }
  .burger--open .burger__button::after {
    top: 50%;
    width: 18px;
    background-color: #011c40;
    transform: rotate(45deg);
  }
  .burger--open .burger__button::before {
    top: 50%;
    width: 18px;
    box-shadow: none;
    background-color: #011c40;
    transform: rotate(-45deg);
  }
  .burger--open .burger__wrapper {
    position: absolute;
    width: 100vw;
    height: 100vh;
    transform: translateX(0%);
    background-color: rgba(1, 24, 64, 0.7);
  }
  .burger--open .burger__list {
    margin-left: 35px;
    height: 102vh;
    padding: 87px 25px;
    align-items: flex-start;
    background-color: #f9fbfd;
  }
}

